import { Navigate } from 'react-router-dom';
import { getAuth } from 'firebase/auth';
import React, { useState, useEffect } from 'react';

const ProtectedRoute = ({ element }) => {
  const auth = getAuth();
  const [user, setUser] = useState(null);
  const [redirecting, setRedirecting] = useState(false);

  useEffect(() => {
    const currentUser = auth.currentUser;
    setUser(currentUser);

    if (!currentUser && !redirecting) {
      setRedirecting(true);
      alert("ท่านยังไม่ได้ล็อกอิน โปรดล็อกอินก่อน");
      setTimeout(() => {
        setRedirecting(false); // รีเซ็ตสถานะการนำทาง
      }, 2000);
    }
  }, [auth, redirecting]);

  if (redirecting) {
    return null; // ให้แสดง nothing ในช่วงที่กำลังหน่วงเวลา
  }

  // ถ้าผู้ใช้ไม่ได้ล็อกอิน ให้ใช้ Navigate เพื่อไปที่หน้า Login
  if (!user) {
    return <Navigate to="/login" />;
  }

  // ถ้าผู้ใช้ล็อกอินแล้ว ให้แสดงหน้า element
  return element;
};

export default ProtectedRoute;
