import React, { useState, useEffect } from "react";
import { Link } from "react-router-dom";
import { getAuth, onAuthStateChanged, signOut } from "firebase/auth";
import "./Navbar.css"; // Import CSS

function Navbar() {
  const [user, setUser] = useState(null);

  useEffect(() => {
    const auth = getAuth();
    const unsubscribe = onAuthStateChanged(auth, (currentUser) => {
      setUser(currentUser);
    });

    return () => unsubscribe(); // Cleanup function to unsubscribe on unmount
  }, []);

  const handleLogout = async () => {
    const auth = getAuth();
    await signOut(auth);
    setUser(null);
  };

  return (
    <nav className="navbar">
      <Link to="/" className="navbar-link">Home</Link>
      {user ? (
        <>
          <span className="navbar-user">Hello, {user.displayName || "User"}</span>
          <button onClick={handleLogout} className="navbar-link logout-btn">Logout</button>
        </>
      ) : (
        <button onClick={() => window.location.href = '/login'} className="navbar-link login-register-btn">Login/Register</button>
      )}
    </nav>
  );
}

export default Navbar;
